.phone {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  color: $default-color;
  text-decoration: none;

  &--black {
    color: $text-color;
  }

  & > span {
    font-size: 1.5625rem;
  }

  & > span > span {
    font-size: 0.9375rem;
    font-family: $mainFont;
    font-weight: $mainFontWeight;
  }

  & span {
    display: block;

    @include for-size(phone-only) {
      display: none;
    }
  }
  & svg {
    margin-right: 0.9375rem;

    @include for-size(phone-only) {
      margin-right: 0;
    }
  }
}
