*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  font-size: $mainFontSize;
  font-weight: $mainFontWeight;
  line-height: $mainLineHeight;
  color: $text-color;

}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

// footer к низу

.wrapper {
  // display: table;
  width: 100%;
  // height: 100%;
  // overflow: hidden;
  min-height: 100%;
}
.main {
  // display: table-row;
  // height: 100%;
  min-height: calc(100% - 400px);

  // box-shadow: inset 5px 0 5px -5px #333;
}

.overlay {
  overflow: hidden;
}

.scroll-content {
  overflow-y: auto;
  height: 100%;
}
