.offices-address {
  padding: $padding-and-margin 0;

  &--step-tour-duraion {
    & .offices-address__inner {
      border-bottom-color: transparent;
    }
  }
  &--index-block {
    & .offices-address__item {
      @include grid-col(
      (
        xs: 12,
        sm: 6
      )
    );
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
    @include grid-row();
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin-bottom: $padding-and-margin;

    @include grid-col(
      (
        xs: 12,
        sm: 6,
        md: 4
      )
    );
  }
  &__inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-bottom: 1px solid $border-base;
  }
}
