.hotel-item {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 0 15px;

  &__row {
    @include grid-row();
  }
  &__gallery {
    margin-bottom: $padding-and-margin;
    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 4
      )
    );
  }
  &__content-tabs {
    margin-bottom: $padding-and-margin;
    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 8
      )
    );
  }

  &__inner {
    padding: $padding-and-margin;
  }

  &__preview {
    display: flex;
    flex-wrap: wrap;
    border: 1px solid $border-base;
    border-radius: 5px;
    // box-shadow: 0 5px 5px 0px #d7d7da;
    overflow: hidden;

    @include grid-row();
  }
  &__pic {
    // @include for-size(tablet-portrait-up) {
    //   flex: 0 0 50%;
    // }
    // @include for-size(tablet-landscape-up) {
    //   flex: 0 0 30%;
    // }

    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 4
      )
    );

    padding: 0;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info {
    padding: $padding-and-margin;
    // @include for-size(tablet-portrait-up) {
    //   flex: 0 0 50%;
    // }
    // @include for-size(tablet-landscape-up) {
    //   flex: 0 0 70%;
    // }

    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 8
      )
    );

    &-header {
      margin-bottom: $padding-and-margin;
    }
    &-rating {
      margin-bottom: $padding-and-margin;
    }
    &-text {
      margin-bottom: calc(var(--padding-and-margin) * 2);
    }
    &-footer {
      margin-bottom: $padding-and-margin;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    &-name {
      @include ff("font-size", $mainTitle);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
    }
    &-address {
      color: #808080;
    }
    &-price {
      margin-bottom: $padding-and-margin;
      @include ff("font-size", $mainTitleh1);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
    }
    & .button {
      margin-bottom: $padding-and-margin;
    }

    &-btn {
      &.active {
        transform: rotate(180deg);
      }
    }
  }
  &__content {
    height: 0;
    opacity: 0;
    overflow: hidden;
    background-color: $bg-color;
    animation: close-height 0.5s forwards;

    @include grid-row();

    &.active {
      opacity: 1;
      overflow: visible;
      transition: 1s opacity;
      animation: open-height 1s forwards;
      box-shadow: inset 0 40px 40px -40px #d7d7da;
    }

    & .content {
      @include for-size(tablet-landscape-up) {
        margin-left: -15px;
      }
    }
  }
}
