.page-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--padding-and-margin) 0;

  @include for-size(tablet-landscape-up) {
    padding: calc(var(--padding-and-margin) * 2) 0;
  }

  &--bg-1 {
    position: relative;
    min-height: 37.5rem;
    background-color: $primary-color;
    background-image: url("../img/bg/bg-1.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 20rem;
      background-image: url("../img/bg/bg-11.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
    }
  }
  &--bg-3 {
    background-color: #fed501;
    background-image: url("../img/bg/bg-3.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0% 0%;
  }
  &--bg-4 {
    background-color: #fefaee;
    background-image: url("../img/bg/bg-4.jpg");
    background-repeat: repeat;
    background-size: cover;
    background-position: -2px 136%;
  }
  &--bg-6 {
    background-color: $bg-color;
  }

  &__row {
    @include grid-row();
  }

  &__col {
    @include grid-col(
      (
        xs: 12,
        sm: 6
      )
    );
  }
  &__info-contacts {
    @include grid-col(
      (
        xs: 12,
        md: 7
      )
    );

    @include for-size(tablet-portrait-up) {
      order: 1;
      padding: $padding-and-margin;
    }
  }
  &__map-contacts {
    @include grid-col(
      (
        xs: 12,
        md: 5
      )
    );
  }

  &__title {
    margin-bottom: $padding-and-margin;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleSection);
    line-height: normal;
  }
  &__title-block {
    margin-bottom: $padding-and-margin;

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleh1);
    line-height: normal;
  }

  & .container {
    position: relative;
  }
  &__link {
    position: absolute;
    top: 0;
    right: 2.5rem;
    display: flex;
    align-items: center;

    color: $primary-color;
    line-height: normal;
    text-decoration: none;

    @include for-size(phone-only) {
      right: 1rem;
      & .page-section__icon {
        display: none;
      }
    }

    &:focus,
    &:hover {
      & .page-section__icon {
        transform: translateX(10%);
        transition: all 0.5s;
      }
    }
  }
  &__icon {
    margin-left: 0.625rem;
    transform: translateX(-10%);
    transition: all 0.5s;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
  }
  &__item {
    flex-grow: 1;

    margin-bottom: var(--mb);
    max-width: calc(100% / var(--count-column) - var(--padding-and-margin));

    @include for-size(phone-only) {
      --count-column: 1;
      --padding-and-margin: 0;
    }
    @include for-size(tablet-portrait-up) {
      margin-right: var(--padding-and-margin);
      --count-column: 2;
    }

    @include for-size(tablet-landscape-up) {
      --count-column: 3;
    }
    @include for-size(desktop-up) {
      --count-column: 4;
    }
  }
}
