.team {
  padding: $padding-and-margin 0;
  & .container {
    padding: 0;
  }
  &__title {
    margin-bottom: $padding-and-margin;
  }
  &__inner {
    padding: $padding-and-margin;
    display: flex;
    flex-wrap: wrap;
    background-color: #f7f7f8;
  }
  &__list {
    @include listReset();
  }
  &__footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: $padding-and-margin;
    width: 100%;
    border-top: 1px solid $default-color;

    & .team__item {
      @include for-size(tablet-portrait-up) {
        flex: 0 0 50%;
      }
      @include for-size(tablet-landscape-up) {
        flex: 0 0 30%;
      }
      @include for-size(desktop-up) {
        flex: 0 0 25%;
      }
    }
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: $padding-and-margin;
  }
  &__item-img {
    margin-right: 1.25rem;
    width: 50px;
    height: 50px;
    background-color: #e6e6e6;
    background-image: url('../img/icons/icon-ava.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;
  }
  &__item-name {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    color: $team-link-color;
  }
  &__item-position {
    max-width: 10.5rem;
  }
  &__item-rating {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item-rating-icon {
    margin-right: 0.625rem;
  }
  &__box {
    margin-bottom: var(--padding-and-margin);
    @include for-size(tablet-portrait-up) {
      flex: 0 0 50%;
    }
    @include for-size(tablet-landscape-up) {
      flex: 0 0 30%;
    }
    @include for-size(desktop-up) {
      flex: 0 0 25%;
    }
  }
}
