.office-address {
  &__title {
    display: block;
    margin-bottom: $padding-and-margin;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleh2);
  }
  &__stations {
    display: flex;
    flex-wrap: wrap;
  }
  &__info {
    display: flex;
    align-items: center;
    margin-bottom: $padding-and-margin;

    & svg {
      width: 30px;
      margin-right: 0.9375rem;
    }
  }
  &__contacts {
    display: flex;
    flex-wrap: wrap;

    &--border {
      padding-top: calc(var(--padding-and-margin) * 2);
      border-top: 1px solid $border-base;
      border-bottom: 1px solid $border-base;
    }
  }
  &__contacts-link {
    display: flex;
    align-items: center;
    margin-right: 0.625rem;
    margin-bottom: calc(var(--padding-and-margin) * 2);

    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    color: $text-color;

    & svg {
      margin-right: 0.625rem;
    }
  }
  &__social {
    display: flex;
    flex-wrap: wrap;

    &-link {
      display: flex;
      align-items: center;
      margin: $padding-and-margin 0;

      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
      color: $text-color;

      @include for-size(tablet-portrait-up) {
        flex: 0 0 48%;
      }

      & svg {
        width: 3.125rem;
      }
    }
  }
}
