.content {
  & .img-float {
    float: left;
    margin-right: $padding-and-margin;
    margin-bottom: $padding-and-margin;
    max-width: 500px;
    width: 100%;
  }
  & p {
    margin-bottom: 1.5rem;
    line-height: normal;
  }
  & h1,
  & h2,
  & h3,
  & h4,
  & h5,
  & h6 {
    line-height: normal;
    margin-bottom: 1.5rem;
  }

  & a {
    color: $link-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: darken($link-color, 9%);
    }
  }

  & ul {
    @include listReset();
    margin-bottom: 1.5rem;

    & li {
      position: relative;
      display: table;
      padding-left: 1.25rem;
      margin-bottom: 0.5rem;
      &::before {
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 15px;
        height: 15px;

        background-image: url("../img/icons/icon-marker.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        border-radius: 50%;
      }
    }
  }
}
