.search-form {
  position: relative;
  z-index: 5;
  width: 100%;
  max-width: 1000px;
  background-color: $default-color;
  border-radius: 10px;
  box-shadow: 0 0 25px darken($border-base, 9%);
  &__inner {
    padding: 0 15px;
  }
  &__title {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    padding-bottom: calc(var(--padding-and-margin) / 2);
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include listReset();
    @include grid-row();
  }
  &__item {
    position: relative;
    z-index: 5;
    display: flex;
    flex-direction: column;
    border-right: 1px solid $border-base;

    &:last-child {
      padding: 0;
      border-right: none;
    }
    &:nth-child(5) {
      border-right: none;
    }

    @include grid-col(
      (
        xs: 12,
        sm: 4,
        md: 4,
        lg: 2
      )
    );

    @include for-size(phone-only) {
      border-right: none;
    }
  }
  &__field {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: calc(var(--padding-and-margin) / 2) 0;
  }
  &__val {
    display: block;
    margin-top: auto;
    margin-bottom: 0;
    padding-right: 20px;
    width: 100%;
    border: none;
    background-repeat: no-repeat;
    background-position: 100% 0;
    background-size: contain;

    &--icon-one {
      background-image: url("../img/icons/icon-search_where.svg");
    }
    &--icon--two {
      background-image: url("../img/icons/icon-search_adlt.svg");
    }

    &--icon-adlt {
      background-image: url("../img/icons/icon-search_adlt.svg");
      background-position: 95% 0;
    }
    &--icon--kids {
      background-image: url("../img/icons/icon-search_kids.svg");
      background-position: 95% 0;
    }
  }
  &__btn {
    position: absolute;
    bottom: -5px;
    right: -15px;
    background-color: transparent;
    &:focus,
    &:hover {
      background-color: transparent;
      & .search-form__btn-icon {
        background-color: transparent;
      }
    }
    &-icon {
      width: 15px;
      height: 15px;
      background-color: transparent;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    padding-bottom: calc(var(--padding-and-margin) / 2);
    border: none;
    &-title {
      padding-top: calc(var(--padding-and-margin) / 2);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
    }
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      @include listReset();
      @include grid-row();
    }
    &-item {
      @include grid-col(
        (
          xs: 6
        )
      );
    }
    &-field {
      position: relative;
      padding: calc(var(--padding-and-margin) / 2) 0;
    }
  }
  &__btn-submit {
    width: 100%;
    height: 100%;
  }
  &__options {
    position: absolute;
    z-index: 7;
    top: 85%;
    left: 0;

    width: 300px;

    background-color: $default-color;
    border-radius: 5px;

    @include for-size(phone-only) {
      position: relative;
      z-index: 5;
      top: 0;
      left: -10px;
      width: auto;
    }
    &.closed {
      height: 0;
      overflow: hidden;
      pointer-events: none;

      animation-name: close-opacity;
      animation-duration: 1s;
    }
    &.active {
      height: 150px;
      overflow: visible;
      overflow-y: auto;
      pointer-events: all;

      animation-name: open-opacity;
      animation-duration: 1s;
    }
    &-list {
      @include listReset();
      padding: var(--padding-and-margin);
    }
    &-item {
      display: flex;
      justify-content: space-between;

      color: $light-color;

      &:not(:last-child) {
        margin-bottom: var(--padding-and-margin);
      }
    }
  }
}
