.filters {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  margin-bottom: $padding-and-margin;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  color: $primary-color;
  &__item {
    display: flex;
    flex-grow: 1;
    margin-right: 0.9375rem;
  }
  &__label {
    white-space: nowrap;
    margin-right: 10px;
  }
  & .custom-select {
    min-width: 4rem;
  }
  & .custom-select-trigger {
    padding-right: 20px;
  }
  & .custom-options {
    left: -110px;
    min-width: 11.25rem;
  }
}
