.accordion {
  padding: $padding-and-margin;
  &--bg {
    background-color: $default-color;
  }
  &--bg-2 {
    background-color: #f5feff;
  }
  &__title {
    position: relative;
    margin-bottom: $padding-and-margin;
    max-width: 90%;
    @include ff('font-size', $mainTitleh2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    color: $acc-color;
    transition: 0.3s;

    &:hover {
      cursor: pointer;
    }
  }
  & .expand-collapse {
    position: absolute;
    top: 0;
    right: -10%;
  }
  &__description {

    max-height: 0;
    line-height: normalgulp;
    color: $text-color;
    overflow: hidden;

    transition: max-height 0.2s ease-out;
    p {
      margin-bottom: $padding-and-margin;
    }
  }
}

