.step-making {
  &__header {
    text-align: center;
    color: $light-color;
  }
  &__title {
    margin-bottom: var(--padding-and-margin);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff('font-size', $mainTitle);
    color: $text-color;
  }
  &__item-title {
    margin-bottom: var(--padding-and-margin);
    margin-top: var(--padding-and-margin);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;

    text-align: center;
  }
}
