.metro-station {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-right: .625rem;
  margin-bottom: .625rem;
  &--blue {
    & .metro-station__icon {
      fill: $primary-color;
    }
  }
  &--yellow {
    & .metro-station__icon {
      fill: $warning-color;
    }
  }
  &--purple {
    & .metro-station__icon {
      fill: $purple-color;
    }
  }
  &__icon {
    fill: red;
    margin-right: .625rem;
  }
}
