.steps {
  display: block;
  margin-right: auto;
  padding: 2.25rem 3.125rem 2.25rem 3.125rem;
  overflow: hidden;

  &__list {
    @include listReset();
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item {
    position: relative;
    padding: $padding-and-margin 0;

    &:not(:last-child) {
      margin-right: $padding-and-margin;
    }

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 130%;
      transform: translateY(-50%);
      width: 9.5rem;
      height: 2px;
      border: 1px dashed #e6e6e6;

      @include for-size(phone-only) {
        display: none;
      }
      @include for-size(tablet-portrait-up) {
        left: 120%;
        width: 3.5rem;
      }
      @include for-size(tablet-landscape-up) {
        width: 7.5rem;
      }
      @include for-size(desktop-up) {
        left: 130%;
        width: 9.5rem;
      }
    }

    &--hidden-sm {
      display: block;
      @include for-size(phone-only) {
        display: none;
      }
    }

    &.active {
      &:not(:last-child)::after {
        border-color: $primary-color;
      }
      & .steps__number {
        background-color: $primary-color;
      }
      & .steps__title {
        color: $text-color;
      }
    }
  }

  &__number {
    padding: 10px 20px;
    font-weight: 600;
    color: #ffffff;
    background-color: #e6e6e6;
    border-radius: 50%;

    @include ff(
      "font-size",
      (
        375px: 20px,
        750px: 25px
      )
    );
  }
  &__title {
    position: absolute;
    left: 50%;
    bottom: -30px;
    width: 12.5rem;
    transform: translateX(-50%);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    text-align: center;
    color: #e6e6e6;
  }

  &__content {
    margin: $padding-and-margin 0;
    padding: $padding-and-margin;
    background-color: $bg-color;
    overflow: hidden;

    &-row {
      align-items: center;
    }
  }
}

