.step-price {
  @include grid-row();
  &__card {
    margin-bottom: $padding-and-margin;
    @include grid-col(
      (
        xs: 12,
        sm: 5,
        lg: 4
      )
    );
  }
  &__info-and-form {
    @include grid-col(
      (
        xs: 12,
        sm: 7,
        lg: 8
      )
    );
  }
  &__info {
    padding-bottom: $padding-and-margin;
    margin-bottom: $padding-and-margin;
    border-bottom: 1px solid $border-base;
    &-header {
      margin-bottom: $padding-and-margin;
    }
    &-rating {
      margin-bottom: $padding-and-margin;
    }
    &-text {
      margin-bottom: calc(var(--padding-and-margin) * 2);
    }
    &-footer {
      margin-bottom: $padding-and-margin;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
    }
    &-name {
      @include ff("font-size", $mainTitle);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
    }
    &-address {
      color: #808080;
    }
    &-price {
      margin-bottom: $padding-and-margin;
      @include ff("font-size", $mainTitle);
    }
    &-count {
      margin-bottom: $padding-and-margin;
      @include ff("font-size", $mainTitle);
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
    }
  }
  &__form {
    & .page-form {
      padding-left: 0;
    }
  }
}
