.navigation {
  // position: relative;
  margin: 0 auto;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  width: 100%;

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-right: 0.9375rem;
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    color: #ffffff;
    text-decoration: none;

    &:nth-child(1) {
      display: none;
    }


    @include for-size(phone-only) {
      justify-content: flex-start;
      margin-bottom: calc(var(--padding-and-margin) * 2);

      &:nth-child(1) {
        display: block;
        padding-top: 50px;
      }

    }

    & svg {
      margin-right: 0.9375rem;
    }
  }

  &__link {
    position: relative;
    display: block;
    color: $text-color;
    text-decoration: none;

    &::before {
      opacity: 0;

      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(-10%, -50%);
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    &:focus,
    &:hover {
      color: $primary-color;
      &:before {
        opacity: 1;
        transform: translate(10%, -50%);
        transition: all 0.5s;
      }
    }
  }
  &__icon {
    width: 1.25rem;
    padding-right: 0.3125rem;
    box-sizing: content-box;
  }
  &__logo-and-phone {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-grow: 0;

    @include for-size(phone-only) {
      justify-content: space-between;
      flex-wrap: nowrap;
      flex-grow: 0;

      width: 70%;
      margin-left: auto;
    }
  }
  &__phone {
    margin-left: 1.25rem;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    flex-grow: 0;

    // margin-left: 1.25rem;

    @include for-size(phone-only) {
      display: none;

      position: absolute;
      z-index: 9;
      background-color: #0075ab;
      padding: 30px 20px;
      top: 0;
      left: 0;
      width: 90%;
      min-height: 100vh;
    }

    &.active {
      display: block;
      animation-name: open-opacity;
      animation-duration: 1s;

      transform: translateX(0);
      transition: transform 1s;

      overflow-y: auto;
    }
    &.closed {
      display: none;
      animation-name: close-opacity;
      animation-duration: 1s;

      transform: translateX(-200%);
      transition: transform 1s;
    }
  }
}

.navigation__toggle {
  display: none;
  @include for-size(phone-only) {
    display: block;
  }
}

.navigation--closed {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 99;
  width: 30px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: 0;

  & svg {
    & rect {
      &:first-child {
        transform: rotate(45deg) translateX(1.5px);
      }
      &:nth-child(2) {
        opacity: 0;
      }
      &:nth-child(3) {
        transform: rotate(-45deg) translateX(-5.5px);
      }
    }
  }
}

.navigation--opened {
  position: absolute;
  top: 50%;
  left: 20px;
  z-index: 5;
  width: 30px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
  transform: translateY(-50%);
}

.navigation__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  list-style: none;
}

.navigation__list li {
  position: relative;
  display: block;
  margin: 0;
  padding-left: 1.25rem;
}

.navigation__list a {
  display: block;
  padding: 0.625rem;
  line-height: normal;
  text-decoration: none;

  line-height: normal;
  font-weight: bold;

  @include ff("font-size", $navLink);
  line-height: 100%;
  text-align: left;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #000000;
}

@media (max-width: 47.9375rem) {
  .navigation--closed .navigation__list {
    height: 60px;
    visibility: hidden;
  }

  .navigation--closed .js-logo {
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    visibility: visible;
  }

  .navigation--closed .navigation__wrapper {
    position: relative;
    min-height: 60px;
  }

  .navigation--nojs .navigation__wrapper {
    position: static;
  }

  .navigation.navigation--opened .navigation__wrapper {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: #ffffff;
    overflow: hidden;
  }

  .navigation.navigation--opened .navigation__list {
    display: block;
    height: auto;

    padding: 2.5rem 0.9375rem;

    justify-content: flex-start;

    & .navigation__list li {
      margin-bottom: 0.625rem;
      padding: 0.625rem 0.625rem 0.625rem 1.25rem;
    }

    & .navigation__list a {
      font-size: 16px;
      color: #ffffff;
      text-align: left;
    }
  }

  .navigation--nojs .js-logo {
    position: relative;
  }
}

