.cloud-and-plane {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  min-height: 300px;
  overflow: hidden;
}
.cloud {
  position: absolute;
  width: 200px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  &--1 {
    top: 60%;
    left: 7%;
    background-image: url("../img/icons/icon-cloud_1.svg");
    animation: scroll 5s linear forwards;
  }
  &--2 {
    top: 20%;
    left: 20%;
    background-image: url("../img/icons/icon-cloud_2.svg");
    animation: scroll 10s linear forwards;
  }
  &--3 {
    top: 60%;
    right: 0;
    background-image: url("../img/icons/icon-cloud_3.svg");
    animation: scroll-right 10s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
  }
  &--4 {
    top: 20%;
    left: 7%;
    background-image: url("../img/icons/icon-cloud_4.svg");
    animation: scroll 15s 5s linear forwards;
  }
  &--5 {
    top: 10%;
    right: 20%;
    background-image: url("../img/icons/icon-cloud_5.svg");
    animation: scroll-right 15s linear forwards;
  }
  &--6 {
    top: 20%;
    right: 0;
    background-image: url("../img/icons/icon-cloud_6.svg");
    animation: scroll-right 10s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
  }
}
.sun {
  top: 10%;
  left: 10%;
  position: absolute;

  width: 200px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url("../img/icons/icon-sun.svg");

  animation: scroll-sun 10s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
}
.plane {
  top: 13%;
  right: 2%;
  z-index: -1;
  position: absolute;

  width: 200px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url("../img/icons/icon-plane.svg");

  animation: scroll-plane 10s linear forwards;
}

@keyframes scroll-right {
  0% {
    transform: translateX(-500px);
    opacity: 0;
  }
  100% {
    // transform: translateX(500px);
    transform: translate(0, 0);
    opacity: 1;
  }
}
@keyframes scroll {
  0% {
    transform: translateX(500px);
    opacity: 0;
  }
  100% {
    // transform: translateX(-500px);
    transform: translate(0, 0);
    opacity: 1;
  }
}
@keyframes scroll-sun {
  0% {
    transform: translate(-500px, -500px);
  }
  100% {
    transform: translate(0, 0);
  }
}
@keyframes scroll-plane {
  0% {
    transform: translate(-1000px, -50px);
  }
  100% {
    transform: translate(0, 0);
  }
}
