.step-cards {
}

#cards {
  width: 19.5em;
  height: 10em;
  position: relative;
  margin: 0;
}
#front,
#back {
  position: absolute;
  width: 14.5em;
  height: 9em;
  border-radius: 0.5em;
}
#front {
  top: 0;
  left: 0;
  background: #ddd;
  z-index: 100;
}
#number {
  width: 100%;
  margin-bottom: 0.3em;
}
#front-fields {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1em;
}
#bank-link {
  position: absolute;
  top: 1em;
  right: 1em;
  width: 320px;
  height: 60px;
  display: block;
  position: relative;
  left: 1em;
  background-size: contain;
  background-position: top left;
  background-repeat: no-repeat;
}
#brand-logo {
  position: absolute;
  bottom: 1em;
  right: 1em;
  text-align: right;
  height: 1.6em;
}
#back {
  bottom: 0;
  right: 0;
  background: #bbb;
  padding-top: 1em;
  padding-right: 1em;
  padding-left: 10.5em;
}
#code {
  width: 100%;
}
.field {
  padding: 0.3em 0.5em;
  border: none;
  font-family: "Anonymous Pro", monospace;
  font-size: 0.9em;
  text-indent: 0.1em;
}
.expired {
  float: left;
  width: 3em;
  margin-right: 0.5em;
  margin-top: 0.3em;
}
.label {
  font-size: 0.5em;
  display: block;
  margin-top: 0.5em;
}
#examples {
  list-style: none;
  padding: 0;
}
.example {
  font-size: 0.5em;
  white-space: nowrap;
  display: inline-block;
  margin-right: 1.5em;
  margin-top: 0.3em;
}
.example-link {
  text-decoration: none;
  color: #07c;
  border-bottom: 1px dashed #07c;
}
.example-link:hover,
.example-link:active {
  color: #c00;
  border-color: #c00;
}
.block {
  margin: 1em 0 0;
}
.block:first-child {
  margin: 0;
}
.block h2 {
  margin: 0 0 0.3em 0;
  font-size: 0.7em;
}
ul {
  margin: 0;
}
#validation {
  font-size: 0.5em;
}
#validation li {
  margin-bottom: 10px;
}
#validation li.valid {
  color: green;
}
#validation li.invalid {
  color: red;
}
#instance {
  font-size: 0.5em;
  line-height: 1.5em;
}
