.social {
  width: 100%;
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    @include listReset();
  }
  &__item {
    &:not(:last-child) {
      margin-right: 1rem;
    }
  }
  &__link {
    display: block;
    text-decoration: none;

    &:focus,
    &:hover {
      & .social__icon {
        transform: scale(1.2);
      }
    }
  }
  &__icon {
    width: 2.5rem;
  }
}
