.page-support {
  &__phone {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: $padding-and-margin 0;
  }
  &__phone-text {
    margin-right: .625rem;
    margin-bottom: .625rem;
  }
  & .phone {
    margin-bottom: .625rem;
  }
  & .phone span {
    @include for-size(phone-only) {
      display: block;
    }
  }
}
