.hotel-gallery {
  display: flex;
  flex-direction: column;

  &__inner {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: $padding-and-margin;
    @include grid-row();
  }

  &__item {
    margin-bottom: 20px;

    border-radius: 0;
    text-decoration: none;

    $grid: (
      gap: 10px
    );

    @include grid-col(
      (
        xs: 4,
        sm: 3,
        md: 4
      ), $grid
    );
  }
  &__pic {
    width: 100%;
    height: 100%;
    min-width: 70px;
    min-height: 80px;
    background-color: #e6e6e6;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  & .link {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
  }
}
