.table-price {
  width: 100%;
  max-width: 100%;
  border-collapse: collapse;
  margin: 0;
  @include for-size(desktop-up) {
    width: calc(100% - var(--padding-and-margin) * 2);
    margin: 0 var(--padding-and-margin);
  }
  &__head {
    @include for-size(phone-only) {
      display: none;
    }
  }
  &__row {
    position: relative;
    max-width: 100%;

    &:not(:last-child) {
      border-bottom: 1px solid $border-base;
    }
    @include for-size(phone-only) {
      display: flex;
      flex-direction: column;
      padding: $padding-and-margin;
    }
  }
  &__title {
    padding: $padding-and-margin;
    color: #808080;
  }
  &__val {
    position: relative;
    padding: $padding-and-margin;
    padding-bottom: calc(var(--padding-and-margin) * 1.5);
    vertical-align: top;
    @include for-size(phone-only) {
      width: 70%;
      max-width: 12.5rem;
      padding: $padding-and-margin 0;
      margin-left: auto;
      font-size: 80%;

      &::before {
        position: absolute;
        content: attr(data-name);
        top: -10px;
        left: 0;
        color: #808080;
      }
    }
    &--price {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-self: center;
    }
    &--tour {
      max-width: 12.5rem;
    }
    &--logo {
      @include for-size(phone-only) {
        position: absolute;
        left: $padding-and-margin;
        top: 50%;
        transform: translateY(-50%);
      }

      & img {
        @include for-size(phone-only) {
          width: 3.75rem;
          height: auto;
          object-fit: contain;
        }
      }
    }
    &-price {
      font-family: $mainFont-bold;
      font-weight: $mainFontWeight-bold;
      @include ff("font-size", $mainTitle);
    }
  }
}
