.step-authorization {
  padding-top: $padding-and-margin;
  @include grid-row();
  &__list {
    @include grid-col(
      (
        xs: 12,
        sm: 8,
        md: 6
      )
    );
    @include grid-offset(
      (
        xs: 0,
        sm: 2,
        md: 3
      )
    );
    @include listReset();
    text-align: center;
  }
  &__title {
    margin-bottom: calc(var(--padding-and-margin) / 2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff('font-size', $mainTitleh2);
  }
  &__desc {
    color: $light-color;
  }
}
